import axios from 'axios'

export const API_HOST = process.env.REACT_APP_API_HOST
// export const API_HOST = process.env.REACT_APP_API_HOST || "https://" + window.location.host


function getLangCodeOfPath() {
  const pathArray = window.location.pathname.split('/');
  let langCode = "de"
  while (pathArray.length > 0 ) {
    langCode = pathArray.pop();
    if (['de', 'en', 'fr'].includes(langCode)) {
      return langCode
    }
  }
  return langCode
}


export async function loadProjectTypes(id) {
  const langCode = getLangCodeOfPath()
  const response = await axios.get(
    `${API_HOST}/${langCode}/dd_views/projecttypes`,
    {
      headers: {
        'Content-Type': 'application/json',
      }
    }
  ).catch(error => {
    throw (error.response)
  })

  return response.data
}

